import MailchimpSubscribe from 'react-mailchimp-subscribe';

import { styled } from '@mui/material/styles';

import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';


const ColorButton = styled(Button)(() => ({
    width: '170px',
    color: 'white',
    backgroundColor: '#1a1a1a',
    '&:hover': {
        backgroundColor: '#1a1a1a',
    }
}));


// a basic form
const CustomForm = ({ status, message, onValidated }) => {
    let email, name;
    const submit = () =>
      email &&
      name &&
      email.value.indexOf("@") > -1 &&
      onValidated({
        EMAIL: email.value,
        NAME: name.value
      });
  
    return (
        <Box
            sx={{
                background: 'white',
                borderRadius: 4,
                padding: 5,
                textAlign: 'center'
            }}
        >
            {status === "sending" && <div style={{ color: "blue" }}>sending...</div>}
            {status === "error" && (
                <div
                    style={{ color: "red" }}
                    dangerouslySetInnerHTML={{ __html: message }}
                />
            )}
            {status === "success" && (
                <div
                    style={{ color: "green" }}
                    dangerouslySetInnerHTML={{ __html: message }}
                />
            )}

            <TextField id="standard-basic" label="Your name" variant="standard" inputRef={node => (name = node)} sx={{ mt: 1, mb: 1 }} />
            <br />
            <TextField id="standard-basic" label="Your email" variant="standard" inputRef={node => (email = node)} sx={{ mb: 3 }} />
            <br />

            <ColorButton variant="contained" onClick={submit} sx={{ mb: 1 }} >Subscribe</ColorButton>
        </Box>
    );
};


function Newsletter() {
    const url = "https://kevinazemi.us20.list-manage.com/subscribe/post?u=c523cd58d198e688533739942&amp;id=b39a610947";

    return (
        <>
            <MailchimpSubscribe
                url={url}
                render={({ subscribe, status, message }) => (
                    <CustomForm
                        status={status}
                        message={message}
                        onValidated={formData => subscribe(formData)}
                    />
                )}
            />
        </>
    );
}

export default Newsletter;